@import-normalize;

:root {
	--bg-color: #010314;

	--subtitle-color: #ffffff;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	&:before,
	&:after {
		box-sizing: inherit;
	}
}

.section {
	&.ticker {
		padding: 20px;
	}
}

.subtitle {
	font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1440 - 320)));
	font-weight: 500;
	text-align: center;
	color: var(--subtitle-color);
	opacity: 0.8;
}

.icon {
	width: 24px;
	min-width: 24px;
	height: 24px;
	min-height: 24px;
	object-fit: contain;
	display: block;
	&.heart {
		background: url(https://ig-launch.test-project.pp.ua/img/heart.svg) no-repeat
			50% 50% / contain;
	}
}

.logo {
	width: 100%;
	min-width: 150px;
	height: 100%;
	min-height: 30px;
	object-fit: contain;
	&.amazon {
		background: url(https://cdn.worldvectorlogo.com/logos/codepen.svg) no-repeat
			50% 50% / contain;
	}
	&.prudential {
		background: url(https://cdn.worldvectorlogo.com/logos/codepen.svg) no-repeat
			50% 50% / contain;
	}
	&.binance {
		background: url(https://cdn.worldvectorlogo.com/logos/codepen.svg) no-repeat
			50% 50% / contain;
	}
	&.nyse {
		background: url(https://cdn.worldvectorlogo.com/logos/codepen.svg) no-repeat
			50% 50% / contain;
	}
	&.nasdaq {
		background: url(https://cdn.worldvectorlogo.com/logos/codepen.svg) no-repeat
			50% 50% / contain;
	}
	&.okx {
		background: url(https://cdn.worldvectorlogo.com/logos/codepen.svg) no-repeat
			50% 50% / contain;
	}
	&.dell {
		background: url(https://cdn.worldvectorlogo.com/logos/codepen.svg) no-repeat
			50% 50% / contain;
	}
	&.bybit {
		background: url(https://cdn.worldvectorlogo.com/logos/codepen.svg) no-repeat
			50% 50% / contain;
	}
	&.tsx {
		background: url(https://cdn.worldvectorlogo.com/logos/codepen.svg) no-repeat
			50% 50% / contain;
	}
	&.nano {
		background: url(https://cdn.worldvectorlogo.com/logos/codepen.svg) no-repeat
			50% 50% / contain;
	}
}

.ticker {
	display: flex;
	flex-direction: column;
	gap: 20px;
	position: relative;
	background-color: var(--bg-color);
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 300px;
		height: 100%;
		background: linear-gradient(270deg, #010314 0%, rgba(1, 3, 20, 0) 100%);
		transform: matrix(-1, 0, 0, 1, 0, 0);
		z-index: 3;
		@media screen and (min-width: 768px) and (max-width: 1239.98px) {
			width: 200px;
		}
		@media screen and (max-width: 767.98px) {
			width: 100px;
		}
	}
	&::after {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		width: 300px;
		height: 100%;
		background: linear-gradient(270deg, #010314 0%, rgba(1, 3, 20, 0) 100%);
		z-index: 3;
		@media screen and (min-width: 768px) and (max-width: 1239.98px) {
			width: 200px;
		}
		@media screen and (max-width: 767.98px) {
			width: 100px;
		}
	}
	&__subtitle {
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 10px;
	}
	&__wrap {
		width: 100%;
		display: flex;
		gap: 20px;
		overflow: hidden;
		position: relative;
	}
	&__content {
		flex: 0 0 auto;
		min-width: 100%;
		height: 35px;
		display: flex;
		gap: 20px;
		animation: ticker 30s linear infinite;
	}
	&__logo {
		width: 150px;
	}
}

@keyframes ticker {
	0% {
		transform: translateX(0);
	}
	100% {
		transform: translateX(-100%);
	}
}
